body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  font-size: 100%; 
  background-image: url(./images/escritorio.png);
  background-repeat: no-repeat;
  background-position: bottom left;
  background-attachment: fixed;
  background-size: 50%;
}

.mainContainer{
  background-repeat: no-repeat;
  background-position: bottom right;
  background-attachment: fixed;

}

.containerSuperior{
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin-bottom: 2%;

}

.header-container{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around
}

.nav-container{
    font-size: 150%;
    text-transform: uppercase;
    color: #d60000;
    font-weight: bold;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}

.navlink {
  font-family: 'Righteous', serif;
  margin:5%;
  text-decoration: none;
  color: #d60000 !important;
  text-align: center;
}

.buttonContainer{
  max-height: 10%;
  display: flex;
    align-content: flex-end;
    justify-content: end;
  
}

#get-another-quote-button {
  text-decoration: none;
  color: #FC7750;
  border: 3px solid #FC7750;
  border-radius: 5px;
  padding: 10px 15px;
  font-weight: bold;
  background: #fff;
  margin-left: 85%;
}
